<h2 id="选择模型" tabindex="-1"><a class="header-anchor" href="#选择模型" aria-hidden="true">#</a> 选择模型</h2>
<p>进入<a href="https://github.com/qsyyke/vscode-live2d-models" target="_blank" rel="noopener noreferrer">这里<ExternalLinkIcon/></a>选择自己喜欢的模型</p>
<p><img src="https://ooszy.cco.vin/img/blog-public/ljz.gif" alt="image-20211212155716585" originSrc="https://ooszy.cco.vin/img/blog-note/image-20211212155716585.png?x-oss-process=style/pictureProcess1" data="aurora"></p>
<p>设置好之后，重新运行<code>npm run dev</code>即可</p>
